.wrapper {
    width: 400px;
    height: 250px;
    margin: 0 50px;
    border: 5px solid #ffffff;
    border-radius: 20px;
    padding: 20px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.display {
    background-color: #ffffff;
    width: 300px;
    height: 160px;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.content {
    color: #36454F;
    font-size: 125px;
    font-weight: 600;
}

.contentFinal {
    color: crimson;
    font-size: 125px;
    font-weight: 600;
}

.buttonsArea {
    box-sizing: border-box;
    width: 300px;
    height: 70px;
    border: 4px solid #ffffff;
    border-radius: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 15px;
}